import { TokenStorage } from "../../DataStructures/Security/TokenStorage";
export function GetRequestHeader(contentType = 'application/json', Accept = 'application/json') {
    var headers = {
        'Content-Type': contentType,
        'Authorization': "Bearer " + TokenStorage.BearerAccessToken,
        'Accept': Accept,
    };
    return headers;
}
export function GetRequestHeaderWithAuthorization(contentType = 'application/json', token = "", Accept = 'application/json') {
    if (token == "") {
        token = TokenStorage.BearerAccessToken;
    }
    var headers = {
        'Content-Type': contentType,
        'Authorization': "Bearer " + token,
        'Accept': Accept
    };
    return headers;
}
export function GetOnlyTokenHeader() {
    let token = TokenStorage.BearerAccessToken;
    const myHeaders = new Headers();
    myHeaders.append('Authorization', 'Bearer ' + token);
    return myHeaders;
}
