export function CheckIfConceptsExistsInArray(conceptList = [], concept) {
    let foundConcept = CreateDefaultConcept();
    if (Array.isArray(conceptList)) {
        const check = conceptList.find(c => c.id === concept.id);
        if (check) {
            foundConcept = check;
        }
    }
    return foundConcept;
}
